import React, { Component } from 'react'
import { connect } from 'react-redux';
import MyColorPicker from './MyColorPicker';
import { MultiSelect } from 'primereact/multiselect';
import { InputText } from 'primereact/inputtext';

class BarGraphForm extends Component {
  state = {
    dataItems: null,
    title: '',
    color: '',
    max: '',
    min: ''
  }

  dataItems = this.props.data.dataItemDefinitions.array;

  componentDidMount() {
    const { tile } = this.props.ui.modal;
    if(tile) {
      const { title, dataItems, color, max, min } = tile.properties;
      this.setState({
        title,
        color,
        max,
        min,
        dataItems
      })
    }
  }

  handleChange = (e) => {
    e.preventDefault();
    const { addData } = this.props;
    const value = e.target.checked ? e.target.checked : e.target.value;
    this.setState({[e.target.name]: value}, () =>{
      addData(this.state);
    });
  }

  render () {
    const { dataItems, title, color, max, min } = this.state;
    return (
      <section className='component-form'>
        <MultiSelect
          style={{maxHeight: '500px'}}
          name='dataItems'
          value={dataItems}
          options={this.dataItems}
          optionLabel='label'
          onChange={this.handleChange}
          placeholder='Choose data items'
          filter
          required
          scrollHeight={'300px'}
        />
        <span className='p-float-label'>
          <InputText
            value={title}
            type='text'
            onChange={this.handleChange}
            name='title'
          />
        <label htmlFor='title'>Title</label>
        </span>
        <span className='p-float-label'>
          <InputText
            value={max}
            type='number'
            onChange={this.handleChange}
            name='max'
          />
        <label htmlFor='max'>Y-Axis Max</label>
        </span>
        <span className='p-float-label'>
          <InputText
            value={min}
            type='number'
            onChange={this.handleChange}
            name='min'
          />
        <label htmlFor='min'>Y-Axis Min</label>
        </span>
        <MyColorPicker color={color} handleChange={this.handleChange} label='Column color:' name='color' />
        <span>If min and max are not specificed the axis will autoscale</span>
      </section>
    )
  }
}

function mapStateToProps({data, ui}) {
  return {data, ui}
}

export default connect(mapStateToProps)(BarGraphForm);
