import React, { Component } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import * as actions from '../../actions';
import { keys } from '../../util/keys'
import { GMap } from 'primereact/gmap';
import { ScaleLoader } from 'react-spinners';
import variables from '../../styles/variables.scss';

const google = window.google;

class Map extends Component {
  state = {
    overlays: [],
    options: {},
    loading : true,
  }

  onMapReady = async () => {
    const { asset } = this.props.data;
    const { showAll } = this.props.tile.properties;
    const assets = showAll ? await this.fetchData() : null;
    if (asset && google) {
      const overlays = assets ? assets.map((ass) => {
        return new google.maps.Marker({
          position:{lat:ass.latitude, lng: ass.longitude},
          title: ass.assetAlias
        })
      }) : [
        new google.maps.Marker({position:{lat: asset.latitude, lng: asset.longitude}, title: asset.assetAlias})
      ];
      const options = {
        center: asset ? {lat: asset.latitude, lng: asset.longitude } : {lat:29.7604, lng: -95.3698},
        zoom: 10
      };
      this.setState({overlays, options, loading:false});
    }
  }

  fetchData = async () => {
    const url = '/api/assets';
    try {
      const request = await axios({
        url: url,
        baseURL: keys.BASE_URL,
        method: 'GET',
        headers: {
          "Content-Type": "application/json"
        }
      });
      return request.data;
    } catch(e) {
      console.log(e);
      return null;
    }
  }

  onOverlayClick = (e) => {
    // console.log(e);
    // let title = e.overlay.title;
    // const content = '<div>' + title + '</div>';
    // const infowindow = new google.maps.InfoWindow({
    //   content: content
    // });
  }

  componentDidUpdate(prevProps) {
    const { properties } = this.props.tile;
    if(prevProps.tile.properties !== properties) {
      //this.setState({loading:true});
      this.onMapReady();
    }
  }

  render () {
    const { asset } = this.props.data;
    const { overlays, loading } = this.state;
    const options = {
      center: asset ? {lat: asset.latitude, lng: asset.longitude } : {lat:29.7604, lng: -95.3698},
      zoom: 10
    };
    if (asset) {
      return (
        <div
          style={{width:'100%', height: '100%'}}
          onMouseDown={e => e.stopPropagation()}
        >
          <GMap
            options={options}
            overlays={overlays}
            onMapReady={this.onMapReady}
            style={{width:'100%', height: '100%'}}
            onMouseDown={e => e.stopPropagation()}
          />
        </div>
      )

    } else {
      return (
        <div style={{position: 'absolute', top: '40%', left:'50%', transform: 'translateX(-50%)', display:loading === true ? 'block' : 'none'}}>
          <ScaleLoader
            sizeUnit={'px'}
            size={30}
            color={variables.fontColor}
          />
        </div>
      )
    }
  }
}

function mapStateToProps({ data }) {
	return { data }
}

export default connect(mapStateToProps, actions)(Map);
