import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";

class Gauge extends Component {
	static defaultProps = {
	}

	chartInit = () => {
		const { id, properties } = this.props.tile;
		//const properties = resolver(this.props.tile.properties, data);
		const min = Number(properties.min);
		const max = Number(properties.max);
		const band1End = Number(properties.band1End);
		const band2End = Number(properties.band2End);

		am4core.options.commercialLicense = true;
		this.chart = am4core.create(id, am4charts.GaugeChart);

		this.chart.paddingRight = 0;
		this.chart.paddingLeft = 0;
		this.chart.paddingTop = 5;
		this.chart.paddingBottom = 0;
		this.chart.marginRight = 0;
		this.chart.marginLeft = 0;
		this.chart.marginTop = 5;
		this.chart.marginBottom = 0;

		this.chart.innerRadius = -8;
		this.chart.startAngle = 135;
		this.chart.endAngle = 405;

		const axis = this.chart.xAxes.push(new am4charts.ValueAxis());
		axis.min = +properties.min;
		axis.max = +properties.max;
		axis.strictMinMax = true;
		const colorSet = new am4core.ColorSet();
		axis.renderer.ticks.template.stroke = '#fff'; //this.chart.colors.getIndex(6);
		axis.renderer.labels.template.radius = 5;
		axis.renderer.ticks.template.strokeOpacity = .35;
		//axis.renderer.ticks
		axis.renderer.grid.template.disabled = true;
		axis.renderer.ticks.template.length = -12;
		axis.hiddenState.properties.opacity = 1;
		axis.hiddenState.properties.visible = true;
		axis.setStateOnChildren = true;
		axis.numberFormatter.bigNumberPrefixes = [
			{ "number": 1000, "suffix" : "k"},
			{ "number": 1000000, "suffix": "M" },
			{ "number": 1e+9, "suffix": "G" },
		]
		axis.numberFormatter.numberFormat = '#,###.#a'

		const range0 = axis.axisRanges.create();
		range0.value = min;
		range0.endValue = band1End ? (min + max)*band1End/max : (min + max)*.5;
		range0.axisFill.fillOpacity = .9;
		range0.axisFill.fill = properties.band1 ? `#${properties.band1}` : colorSet.getIndex(0);

		const range1 = axis.axisRanges.create();
		range1.value = band1End ? (min + max)*band1End/max : (min + max)*.5;
		range1.endValue = band2End ? (max - min)*band2End/max + min : (max - min)*.8 + min;
		range1.axisFill.fillOpacity = .9;
		range1.axisFill.fill = properties.band2 ? `#${properties.band2}` : colorSet.getIndex(2);

		const range2 = axis.axisRanges.create();
		range2.value = band2End ? (max - min)*band2End/max + min : (max - min)*.8 + min;
		range2.endValue = max;
		range2.axisFill.fillOpacity = .9;
		range2.axisFill.fill = properties.band3 ? `#${properties.band3}` : colorSet.getIndex(4);

		this.label = this.chart.radarContainer.createChild(am4core.Label);
		this.label.isMeasured = false;
		this.label.horizontalCenter = "middle";
		this.label.verticalCenter = "bottom";
		this.label.y = 70;
		this.label.fontSize = 20;
		this.label.text = `${properties.min} ${properties.unit ? properties.unit : ''}`;

		const Title = this.chart.titles.create();
		Title.text = properties.name !== '' ? properties.name : properties.dataItem ? properties.dataItem.label : '';
		Title.marginBottom = 15;
		this.hand = this.chart.hands.push(new am4charts.ClockHand());
		//this.hand.showValue(properties.min, 1000, am4core.ease.cubicOut);
		this.updateValue();
	}

	componentDidMount() {
		this.chartInit()
	}

	updateValue = () => {
		const { properties } = this.props.tile;
		const currentData = this.props.currentData.object;
		try {
			//const { label } = properties.dataItem;
			const value = currentData[properties.dataItem.dataItemId].value.toFixed(2);
			this.hand.showValue(+value, 1000, am4core.ease.cubicOut);
			this.label.text = `${value} ${properties.unit}`;
		} catch(e) {
			this.hand.showValue(properties.min, 1000, am4core.ease.cubicOut);
			this.label.text = `undefined`;
		}
	}

	componentDidUpdate(prevProps) {
		const { properties } = this.props.tile;
		if(prevProps.tile.properties !== properties) {
			this.chart.dispose();
			this.chartInit();
		}
		this.updateValue();

	}

	componentWillUnmount() {
	 if (this.chart) {
		 this.chart.dispose();
	 }
  }

	render() {
		const { id } = this.props.tile;
		return (
			<div
				id={id}
				style={{width: "100%", height: "100%"}}
			>
			</div>
		)
	}
}

function mapStateToProps({ currentData }) {
	return { currentData }
}

export default connect(mapStateToProps)(Gauge)
