const activeTab = (state={name:'default', index:0}, action) => {
  switch(action.type) {
    case 'CHANGE_TAB':
      if (action.tab) {
        return action.tab;
      } else {
        return state;
      }
    // case 'DELETE_TAB':
    //   return state;
    default:
      return state
  }
}

export default activeTab;
