const currentData = (state={}, action) => {
  switch(action.type) {
    case 'CURRENT_DATA': {
      if(action.payload.status === 200) {
        const currentDataArray = action.payload.data;
        const currentDataObject = currentDataArray.reduce((obj, di) => {
          obj[di.dataItemId] = {...di};
          return obj;
        }, {});
        return {
          ...state,
          array: currentDataArray,
          object: currentDataObject
        };
      } else {
        console.log(action.payload)
        return {
          ...state,
          array: [],
          object: {}
        };
      }
    }
    default:
      return state;
  }
}

export default currentData;
