import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Dropdown } from 'primereact/dropdown';
import MyColorPicker from './MyColorPicker';
import { Checkbox } from 'primereact/checkbox';

class IndicatorForm extends Component {
  state = {
    dataItem: undefined,
    labelColor: '',
    valueColor: '',
    backgroundColor: '',
    timestamp: false
  }

  static defaultProps = {}

  dataItems = this.props.data.dataItemDefinitions.array;

  componentDidMount() {
    const { tile } = this.props.ui.modal;
    if(tile) {
      const { dataItem, labelColor, valueColor, backgroundColor, timestamp } = tile.properties;
      //const newDi = this.dataItems.filter((di) => di.label === dataItem.label);
      this.setState({
        dataItem: dataItem,
        labelColor,
        valueColor,
        backgroundColor,
        timestamp
      })
    }
  }

  handleChange = (e) => {
    e.preventDefault();
    const { addData } = this.props;
    const value = e.target.checked ? e.target.checked : e.target.value;
    this.setState({[e.target.name]: value}, () =>{
      addData(this.state);
    });
  }

  render() {
    const { dataItem, labelColor, valueColor, backgroundColor, timestamp } = this.state;
    return (
      <section className='component-form'>
        <Dropdown
          style={{width:'100%'}}
          optionLabel={'label'}
          name='dataItem'
          value={dataItem}
          onChange={this.handleChange}
          options={this.dataItems}
          placeholder='Select a data item'
          filter={true}
          filterBy='label'
          required={true}
        />
        <MyColorPicker
          color={labelColor}
          handleChange={this.handleChange}
          label='Label Color'
          name='labelColor'
        />
        <MyColorPicker
          color={valueColor}
          handleChange={this.handleChange}
          label='Value Color'
          name='valueColor'
        />
        <MyColorPicker
          color={backgroundColor}
          handleChange={this.handleChange}
          label='Background'
          name='backgroundColor'
        />
        <Checkbox checked={timestamp} onChange={this.handleChange} name='timestamp' />
        <label htmlFor='timestamp' className='p-checkbox-label'>Show Timestamp</label>
      </section>
    )
  }
}

function mapStateToProps({ data, ui }) {
  return { data, ui }
}

export default connect(mapStateToProps)(IndicatorForm);
