import React, { Component } from 'react';
import { connect } from 'react-redux';
import { FiX } from 'react-icons/fi';
import { Dropdown } from 'primereact/dropdown';
import { InputText } from 'primereact/inputtext';
import { Button } from 'primereact/button';
import { ColorPicker } from 'primereact/colorpicker';

class MultiAxesConfig extends Component {
  state = {
    axes: [
      {
        label: 'yaxis0',
        min: '',
        max: '',
      }
    ],
    series: [
      {
        dataItem: null,
        axis: null,
        color: ''
      }
    ]
  }
  dataItems = this.props.data.dataItemDefinitions.array;

  componentDidMount() {
    const { tile } = this.props.ui.modal;
    if(tile) {
      const { axes, series } = tile.properties;
      if(series.length !== 0) {
        this.setState({series});
      }
      if(axes.length !== 0) {
        this.setState({axes});
      }
    }
  }

  handleChange = (e, i, type) => {
    if(type === 'axis') {
      const axes = [...this.state.axes];
      axes[i][e.target.name] = e.target.value;
      this.setState({axes});
    } else if (type === 'series') {
      const series = [...this.state.series];
      series[i][e.target.name] = e.target.value;
      this.setState({series});
    }
  }

  addAxis = (e) => {
    e.preventDefault();
    const length = this.state.axes.length;
    this.setState(prevState => ({axes: [
      ...prevState.axes,
      {
        label:`yaxis${length}`,
        min: '',
        max:'',
      }
    ]}))
  }

  addSeries = (e) => {
    e.preventDefault();
    this.setState(prevState => ({series: [
      ...prevState.series,
      {
        dataItem: '',
        axis: '',
        color: ''
      }
    ]}))
  }

  renderSeries = (plot, i) => {
    const { series, axes } = this.state;
    return (
      <div key={i}style={{width:'100%', marginBottom:'1rem'}}>
        <Dropdown
          name='dataItem'
          className='multiaxis-form-field'
          style={{width:'30%', marginLeft: '0'}}
          options={this.dataItems}
          optionLabel='label'
          value={series[i].dataItem}
          onChange={(e) => this.handleChange(e, i, 'series')}
          filter
          filterBy='label'
          placeholder='Choose data item'
          required
        />
        <Dropdown
          name='axis'
          className='multiaxis-form-field'
          style={{width:'30%'}}
          options={axes}
          optionLabel='label'
          value={series[i].axis}
          onChange={(e) => this.handleChange(e, i, 'series')}
          filter
          placeholder='Choose Y-Axis'
          required
        />
        <ColorPicker
          name='color'
          className='multiaxis-form-field'
          style={{width:'10%'}}
          value={series[i].color}
          onChange={(e) => this.handleChange(e, i, 'series')}
          defaultColor='000000'
        />
        <span
          className='multiaxis-form-field'
          style={{width: '10%',textAlign: 'center', verticalAlign: 'middle'}}
        >
          <FiX size={'1.5rem'} onClick={(e) => this.handleDelete(e, i, 'series')} style={{cursor:'pointer'}} />
        </span>
      </div>
    )
  }

  renderAxes = (axis, i) => {
    const { axes } = this.state;
    return (
      <div key={i}style={{width:'100%', marginBottom: '1rem'}}>
        <InputText
          style={{width:'30%', marginLeft: '0'}}
          className='multiaxis-form-field'
          type='text'
          value={axes[i].label}
          onChange={(e) => this.handleChange(e, i, 'axis')}
          name='label'
        />
        <InputText
          className='multiaxis-form-field'
          name='min'
          style={{width:'20%'}}
          value={axes[i].min}
          onChange={(e) => this.handleChange(e, i, 'axis')}
          placeholder='min'
          type='number'
        />
        <InputText
          className='multiaxis-form-field'
          name='max'
          style={{width:'20%'}}
          value={axes[i].max}
          onChange={(e) => this.handleChange(e, i, 'axis')}
          placeholder='max'
          type='number'
        />
        <span
          className='multiaxis-form-field'
          style={{width: '10%',textAlign: 'center', verticalAlign: 'middle'}}
        >
          <FiX size={'1.5rem'} onClick={(e) => this.handleDelete(e, i, 'axis')} style={{cursor:'pointer'}} />
        </span>
      </div>
    )
  }

  handleDelete = (e, index, type) => {
    const { axes, series } = this.state;
    if(type === 'axis') {
      this.setState({axes: axes.filter((axis, i) => i !== index)})
    } else if(type === 'series') {
      this.setState({series: series.filter((series, i) => i !== index)})
    }
  }

  render () {
    const { axes, series } = this.state;
    const { submitModal, closeModal } = this.props;

    return (<>
      <div style={{maxHeight: '700px', overflow: 'scroll'}}>
      <div style={{marginBottom: '1rem'}}>
        <div style={{marginBottom: '1rem'}}>
          <span className='multiaxis-form-header' style={{width:'30%', marginLeft:'0'}}>Label</span>
          <span className='multiaxis-form-header' style={{width:'20%'}}>Minimum</span>
          <span className='multiaxis-form-header' style={{width:'20%'}}>Maximum</span>
          <span className='multiaxis-form-header' >Delete</span>
        </div>
        {axes.map(this.renderAxes)}
        <Button label="Add Axis" onClick={this.addAxis} style={{marginRight: '1rem'}}/>
        <span>Note: If no min/max is specified the axis will autoscale.</span>
      </div>

      <div>
        <div style={{marginBottom: '1rem'}}>
          <span className='multiaxis-form-header' style={{width:'30%', marginLeft:'0'}}>Data Item</span>
          <span className='multiaxis-form-header' style={{width:'30%'}}>Y Axis</span>
          <span className='multiaxis-form-header' style={{width:'10%'}}>Color</span>
          <span className='multiaxis-form-header' >Delete</span>
        </div>
        {series.map(this.renderSeries)}
      <Button style={{marginRight: '1rem'}} label="Add Series" onClick={this.addSeries} />
      <span>Note: Color will default if none specifically selected.</span>
      </div>
      <div className='modal-footer'>
        <Button label="Submit" icon="pi pi-check" onClick={(e) => submitModal(e, axes, series)} />
        <Button label="Close" icon="pi pi-times" onClick={closeModal} style={{marginLeft:'1rem', background: '#e53935', border: '1px solid #e53935'}} />
      </div>
    </div>
    </>)
  }
}

function mapStateToProps({ data, ui }) {
  return { data, ui };
}

export default connect(mapStateToProps)(MultiAxesConfig);
