import React, { Component} from 'react';
import { connect } from "react-redux";
import { Sidebar as PrimeSidebar } from 'primereact/sidebar';
import { Button } from 'primereact/button';
import { Dropdown } from 'primereact/dropdown';
import * as actions from '../actions';
import GaugeForm from '../components/forms/GaugeForm';
import BasicGraphForm from '../components/forms/BasicGraphForm';
import TextBoxForm from '../components/forms/TextBoxForm';
import AdvancedGraphForm from '../components/forms/AdvancedGraphForm';
import TableForm from '../components/forms/TableForm';
import CustomTableForm from '../components/forms/CustomTableForm';
import MapForm from '../components/forms/MapForm';
import CylinderGaugeForm from '../components/forms/CylinderGaugeForm';
import SolidGaugeForm from '../components/forms/SolidGaugeForm';
import IframeTileForm from '../components/forms/IframeTileForm';
import IndicatorForm from '../components/forms/IndicatorForm';
import BarGraphForm from '../components/forms/BarGraphForm';

const TileOptions = [
  {
    name: 'Basic Graph',
    form: BasicGraphForm,
    value: 'BasicGraph'
  },
  {
    name: 'Advanced Graph',
    form: AdvancedGraphForm,
    value: 'AdvancedGraph'
  },
  {
    name: 'Bar Graph',
    form: BarGraphForm,
    value: 'BarGraph'
  },
  {
    name: 'Indicator',
    form: IndicatorForm,
    value: 'Indicator'
  },
  {
    name: 'Dial Gauge',
    form: GaugeForm,
    value: 'Gauge'
  },
  {
    name: 'Cylinder Gauge',
    form: CylinderGaugeForm,
    value: 'CylinderGauge'
  },
  {
    name: 'Solid Gauge',
    form: SolidGaugeForm,
    value: 'SolidGauge'
  },
  {
    name: 'Text/Image',
    form: TextBoxForm,
    value: 'TextBox'
  },
  {
    name: 'Basic Table',
    form: TableForm,
    value: 'Table'
  },
  {
    name: 'Custom Table',
    form: CustomTableForm,
    value: 'CustomTable'
  },
  {
    name: 'Google Map',
    form: MapForm,
    value: 'Map'
  },
  {
    name: 'iFrame',
    form: IframeTileForm,
    value: 'IframeTile'
  },
]

class SideBar extends Component {
  state = {
      properties: {type: null},
  }
  //form data
  // validate = () => {
  //   if (this.form.current.reportValidity()) {
  //     this.setState({valid:true});
  //   } else {
  //     this.setState({valid:false});
  //   }
  // }
  handleFormSubmit = (e) => {
    // ADD TILE 
    e.preventDefault();
    const { activeTab } = this.props;
    const properties = this.state.properties;
    this.props.addTile(properties, activeTab);
    this.setState({properties: {type: null}});
  }

  handleSave = () => {
    const {tiles, layouts, tabs} = this.props;
    this.props.saveDashboard(tiles, layouts, tabs)
    .then(() => alert('Dashboard has been saved!'));
  }

  handleChange = (e) => {
    e.preventDefault();
    this.setState({properties: {[e.target.name]: e.value}, valid: true});
  }

  handleData = (formData) => {
    this.setState({properties:{...this.state.properties, ...formData}})
  }

  renderForm = () => {
    const { type } = this.state.properties;
    if(type) {
      const Form = type.form;
      return <Form addData={this.handleData} />
    }
    return <div style={{marginBottom: '0'}}></div>
  }

  render () {
    //const { valid } = this.state;
    const { ui, toggleSidebar } = this.props;
    return (
      <PrimeSidebar
        className={'side'}
        visible={ui.sidebar}
        onHide={toggleSidebar}
        style={{border: '1px solid #595959'}}
      >
        <section style={{paddingTop: '2rem'}}>
          <div>
            <Button className='p-button-raised' onClick={this.handleSave} label={'Save Dashboard'}/>
          </div>
          <form onSubmit={this.handleFormSubmit} className='sidebar-form' >
            <Dropdown
              options={TileOptions}
              style={{width:'100%'}}
              value={this.state.properties.type}
              onChange={this.handleChange}
              name='type'
              placeholder='Select Tile Type'
              optionLabel='name'
            />
            {this.renderForm()}
            <div>
              <Button label={'Add Tile'} type='submit'/>
            </div>
          </form>
        </section>
      </PrimeSidebar>
    )
  }
}

function mapStateToProps({ tiles, layouts, tabs, activeTab, ui }) {
  return { tiles, layouts, tabs, activeTab, ui }
}

export default connect(mapStateToProps, actions)(SideBar);
