import React, { Component } from 'react';
import { connect } from 'react-redux';

class TextBox extends Component {
  static defaultProps = {
    data: undefined //{}
  }

  componentDidMount() {
  }

  componentWillUnmount() {
  }


  substitutions = (text) => {
    const { currentData } = this.props;
    const { attributes, asset, assetDef } =  this.props.data;
    try {
      return eval('`'+text+'`');
    } catch(e) {
      return text;
    }
  }

  render () {
    const { properties } = this.props.tile;
    const { text } = properties;
    return (
      <section style={{width: '100%', height: '100%', position: 'relative'}} >
        <div dangerouslySetInnerHTML={{__html: this.substitutions(text)}}>
        </div>
      </section>
    )
  }
}

function mapStateToProps({ data, currentData }) {
	return { data, currentData }
}

export default connect(mapStateToProps)(TextBox);
