import React from 'react'
import { connect } from 'react-redux';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";

class BarGraph extends React.Component {
  static defaultProps = {}

  componentDidMount() {
    this.chartInit();
  }

  componentWillUnmount() {
    if (this.chart) {
      this.chart.dispose();
    }
  }

  getData = () => {
    const { currentData } = this.props;
    const { dataItems } = this.props.tile.properties;
    return dataItems.map((dataItem) => {
      return currentData.array.find((cd) => dataItem.dataItemId === cd.dataItemId)
    });
  }

  chartInit = () => {
    const { id, properties } = this.props.tile;
    const { color, title, max, min } = properties;

    am4core.options.commercialLicense = true;
    this.chart = am4core.create(id, am4charts.XYChart);

    this.chart.paddingRight = 5;
    this.chart.paddingLeft = 0;
    this.chart.paddingTop = 15;
    this.chart.paddingBottom = 0;
    this.chart.marginRight = 5;
    this.chart.marginLeft = 0;
    this.chart.marginTop = 5;
    this.chart.marginBottom = 0;

    this.chart.data = this.getData();

    const categoryAxis = this.chart.xAxes.push(new am4charts.CategoryAxis());
    categoryAxis.dataFields.category = "label";
    categoryAxis.renderer.grid.template.location = 0;
    categoryAxis.renderer.labels.template.rotation = 270+45;
    categoryAxis.renderer.labels.template.horizontalCenter = "right";
    categoryAxis.renderer.labels.template.verticalCenter = "right";
    categoryAxis.renderer.labels.template.paddingBottom = 0
    categoryAxis.renderer.minGridDistance = 30;

    const valueAxis = this.chart.yAxes.push(new am4charts.ValueAxis());
    valueAxis.numberFormatter.bigNumberPrefixes = [
      { "number": 1000, "suffix" : "k"},
      { "number": 1000000, "suffix": "M" },
      { "number": 1e+9, "suffix": "G" },
    ]
    valueAxis.numberFormatter.numberFormat = '#,###.##a';
    if(max !== '' && min !== '') {
      valueAxis.max = +max;
      valueAxis.min = +min;
      valueAxis.strictMinMax = true;
    }

    const series = this.chart.series.push(new am4charts.ColumnSeries());
    series.dataFields.valueY = "value";
    series.dataFields.categoryX = "label";
    series.name = "Value";
    if (color !== '') {
      series.columns.template.stroke = am4core.color(`#${color}`);
      series.columns.template.fill = am4core.color(`#${color}`);
    }
    series.columns.template.tooltipText = "{categoryX}: [bold]{valueY}[/]";
    series.columns.template.fillOpacity = .8;

    const columnTemplate = series.columns.template;
    columnTemplate.strokeWidth = 1;
    columnTemplate.strokeOpacity = .9;

    if (title !== '') {
      const Title = this.chart.titles.create();
      Title.text = title;
      Title.marginBottom = 5;
    }
  }

  componentDidUpdate(prevProps) {
    const { properties } = this.props.tile;
    //const { currentData } = this.props.data;
    if(prevProps.tile.properties !== properties) { // on edit submit
      this.chart.dispose();
      this.chartInit();
    }
    try {
      this.chart.data = this.getData();
      this.chart.invalidateRawData();
    } catch(e) {
      console.log(e);
      // do nothing
    }
  }

  render () {
    const { id } = this.props.tile;
		return (
			<div
				id={id}
				style={{width: "100%", height: "100%"}}
			>
			</div>
		)
  }
}

function mapStateToProps({ currentData }) {
  return { currentData }
}

export default connect(mapStateToProps)(BarGraph);


