import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";

class SolidGauge extends Component {
	static defaultProps = {}

	chartInit = () => {
		const { id, properties } = this.props.tile;
		this.chart = am4core.create(id, am4charts.RadarChart);
		this.chart.paddingRight = 5;
		this.chart.paddingLeft = 0;
		this.chart.paddingTop = 5;
		this.chart.paddingBottom = 0;
		this.chart.marginRight = 5;
		this.chart.marginLeft = 0;
		this.chart.marginTop = 5;
		this.chart.marginBottom = 0;
		this.chart.startAngle = -90;
		this.chart.endAngle = 180;
		this.chart.innerRadius = am4core.percent(10);
		// Set number format
		this.chart.numberFormatter.numberFormat = "#.##";
		// Create axes
		const categoryAxis = this.chart.yAxes.push(new am4charts.CategoryAxis());
		categoryAxis.dataFields.category = "category";
		categoryAxis.renderer.grid.template.location = 0;
		categoryAxis.renderer.grid.template.strokeOpacity = 0;
		categoryAxis.renderer.labels.template.horizontalCenter = "right";
		categoryAxis.renderer.labels.template.fontWeight = 300;
		categoryAxis.renderer.labels.template.adapter.add("fill", (fill, target) => {
		  return (target.dataItem.index >= 0) ? this.chart.colors.getIndex(target.dataItem.index) : fill;
		});
		categoryAxis.renderer.minGridDistance = 10;

		const valueAxis = this.chart.xAxes.push(new am4charts.ValueAxis());
		valueAxis.renderer.grid.template.strokeOpacity = 0;
		valueAxis.min = 0;
		valueAxis.max = 100;
		valueAxis.strictMinMax = true;
		valueAxis.renderer.labels.template.adapter.add("text", (text) => {
			if(text >= 100) {
				return `${properties.max} ${properties.unit ? properties.unit : ''}`;
			}
			return text + "%";
		});

		// Create series
		const series1 = this.chart.series.push(new am4charts.RadarColumnSeries());
		series1.dataFields.valueX = "full";
		series1.dataFields.categoryY = "category";
		series1.clustered = false;
		series1.columns.template.fill = new am4core.InterfaceColorSet().getFor("alternativeBackground");
		series1.columns.template.fillOpacity = 0.08;
		series1.columns.template.cornerRadiusTopLeft = 20;
		series1.columns.template.strokeWidth = 0;
		series1.columns.template.radarColumn.cornerRadius = 20;

		const series2 = this.chart.series.push(new am4charts.RadarColumnSeries());
		series2.dataFields.valueX = "value";
		series2.dataFields.categoryY = "category";
		series2.clustered = false;
		series2.columns.template.strokeWidth = 0;
		series2.columns.template.tooltipText = `[bold]{raw} ${properties.unit ? properties.unit : ''}`;
		series2.columns.template.radarColumn.cornerRadius = 20;

		series2.columns.template.adapter.add("fill", (fill, target) => {
		  return this.chart.colors.getIndex(target.dataItem.index);
		});

		// Add cursor
		this.chart.cursor = new am4charts.RadarCursor();
	}

	componentDidMount() {
		this.chartInit();
	}

	componentDidUpdate(prevProps) {
		const { properties } = this.props.tile;
		const { currentData } = this.props;
		if(prevProps.tile.properties !== properties) {
			this.chart.dispose();
			this.chartInit();
		}
		if(properties.dataItems && currentData) {
			const newData = [];
			for(let i = 0; i < properties.dataItems.length; i++) {
				const di = properties.dataItems[i];
				const newDi = currentData.object[di.dataItemId];
				newData[i] = {
					'category': newDi.label,
					'value': (newDi.value/properties.max)*100,
					'raw': newDi.value,
					'full': properties.max
				}
			}
			this.chart.data = newData;
		}

	}

	componentWillUnmount() {
	 if (this.chart) {
		 this.chart.dispose();
	 }
	 // update on new current data call
	 // update this.chart.data
 }

	render() {
		const { id } = this.props.tile;
		return (
			<div
				id={id}
				style={{width: "100%", height: "100%"}}
			>
			</div>
		)
	}
}

function mapStateToProps({ currentData }) {
	return { currentData }
}

export default connect(mapStateToProps)(SolidGauge)
