import React, { Component } from 'react';
import { connect } from 'react-redux';
import { InputText } from 'primereact/inputtext';
import { Dropdown } from 'primereact/dropdown';
import MyColorPicker from './MyColorPicker';

class CylinderGaugeForm extends Component {
  state = {
    dataItem: null,
    min : '',
    max: '',
    unit: '',
    name: '',
    fillColor: ''
  }

  dataItems = this.props.data.dataItemDefinitions.array;

  componentDidMount() {
    // populates fields when editing
    const { tile } = this.props.ui.modal;
    if(tile) {
      const { dataItem, name, max, unit, color } = tile.properties;
      this.setState({
        dataItem,
        name,
        max,
        unit,
        color
      })
    }
  }

  handleChange = (e) => {
    e.preventDefault();
    const {addData} = this.props;
    this.setState({[e.target.name]: e.target.value}, () =>{
      addData(this.state);
    });
  }

  render() {
    const { dataItem, max, unit, color, name } = this.state;
    return (
      <div className='component-form'>
        <Dropdown
          style={{width:'100%'}}
          optionLabel={'label'}
          name='dataItem'
          value={dataItem}
          onChange={this.handleChange}
          options={this.dataItems}
          placeholder='Select a data item'
          filter={true}
          filterBy='label'
          required
        />
        <span className='p-float-label'>
          <InputText
            value={name}
            type='text'
            onChange={this.handleChange}
            name='name'
          />

        <label htmlFor='name'>Name</label>
        </span>
      <span className='p-float-label'>
        <InputText
          value={max}
          type='number'
          onChange={this.handleChange}
          name='max'
          required
        />

      <label htmlFor='max'>Max*</label>
      </span>
      <span className='p-float-label'>
        <InputText
          value={unit}
          type='text'
          onChange={this.handleChange}
          name='unit'
        />
      <label htmlFor='unit'>Unit</label>
        </span>
        <MyColorPicker color={color} handleChange={this.handleChange} label='Fill Color:' name='color' />
        <p style={{fontSize: '12px', marginTop: 0}}>
          {'Fill color will default if not specifically selected'}
        </p>
      </div>

    );
  }
}

function mapStateToProps({data, ui}) {
  return {data, ui}
}

export default connect(mapStateToProps)(CylinderGaugeForm);

