import axios from 'axios';
import { getParameterByName, guid } from '../util';
import { keys } from '../util/keys';
///////// GENERAL ACTIONS ////////////
export const saveDashboard = (tiles, layouts, tabs) => async dispatch => {
  const dashId = getParameterByName('dashboardId');
  const url = `/api/dashboards/${dashId}`;
  const request = await axios({
    url: url,
    baseURL: keys.BASE_URL,
    method: 'POST',
    headers: {
      "Content-Type": "application/json",
      "Authorization": keys.TOKEN,
    },
    data: JSON.stringify({ tiles, layouts, tabs })
  })
  dispatch({
    type: 'SAVE_DASHBOARD',
    payload: request.data
  });
}

export const loadDashboard = () => async (dispatch) => {
  let dashId = getParameterByName('dashboardId');
  const assetDefId = getParameterByName('assetDefinitionId');
  const assetId = getParameterByName('assetId');
  if (!dashId) {
    const request = await axios({
      url: `/api/assetDefinitions/${assetDefId}/Dashboards`,
      baseURL: keys.BASE_URL,
      method: 'GET',
      headers: {
        "Content-Type": "application/json",
        "Authorization": keys.TOKEN
      }
    })
    if(request.data.length !== 0) {
      dashId = request.data[0].dashboardId;
    } else {
      const newDash = {
      	"dashboardId":0,
      	"dashboardName":"assetdef16dashboard",
      	"systemDefinitionId":null,
      	"assetDefinitionId":assetDefId,
      	"config":" "
      }
      const postNewDash = await axios({
        url: '/api/dashboards',
        baseURL: keys.BASE_URL,
        method: 'POST',
        headers: {
          "Content-Type": "application/json",
          "Authorization": keys.TOKEN,
        },
        data: JSON.stringify(newDash)
      });
      dashId = postNewDash[0].dashboardId;
      alert('No dashboard for this asset was found, creating a new one');
      console.log(dashId);
    }
    // add dashboardId parameter
    var newurl = window.location.protocol + "//" + window.location.host + window.location.pathname + `?dashboardId=${dashId}&assetDefinitionId=${assetDefId}&assetId=${assetId}`;
    window.history.pushState({ path: newurl }, '', newurl);
  }
  const request = await axios({
    url: `/api/dashboards/${dashId}`,
    baseURL: keys.BASE_URL,
    method: 'GET',
    headers: {
      "Content-Type": "application/json",
      "Authorization": keys.TOKEN
    }
  });
  dispatch({
    type: 'LOAD_DASHBOARD',
    payload: request
  })

}

