import React, { Component } from 'react';
import { connect } from "react-redux";
import Draggable from 'react-draggable';
import { FiPlus,FiX } from 'react-icons/fi';
import {InputText} from 'primereact/inputtext';
import { Button } from 'primereact/button';
import { guid } from '../util';
import * as actions from '../actions';

class Tabs extends Component {
  state = {
    nextTab: this.props.tabs.length+1, // only used for next tabs default name
    visible: false,
    name: '',
    type: ''
  }

  renderTab = (tab, i) => {
    const { activeTab, ui } = this.props;
    const viewOnly = ui.viewOnly ? 'none' : ''
    let className = 'tab-item';
    if(tab.index === activeTab.index) {
      className += ' tab-active'
    }
    return (
      <li
        key={i}
        className={className}
        style={{textAlign: 'center'}}
        onClick={() => this.props.changeTab(tab)}
      >
        <span
          name='edit'
          onDoubleClick={ui.viewOnly ? null : (e) => this.handleTabRename(e,tab)}
          title={`${tab.name}\nDouble click to edit tab name`}
        > {tab.name}
        </span>
        <span  style={{display:viewOnly}} className={`tab-delete`} title='Click to delete tab'>
          <FiX
            style={{height:'1rem'}}
            className='tab-delete tab-icon'
            size={'1rem'}
            onClick={(e) => this.handleDeleteTab(e, i)}
          />
        </span>
      </li>)
  }

  handleTabRename = (e, tab) => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({name: tab.name, type: tab});
    this.openModal();
  }

  handleAddTab = (e) => {
    this.setState({type:'add'})
    this.openModal();
  }

  handleDeleteTab = (e, index, id) => {
    e.stopPropagation();
    const { deleteTab, changeTab, tabs, activeTab } = this.props;
    if(window.confirm('Are you sure you want to delete this tab?')) {
      deleteTab(index, id);
      if(activeTab.index === index) {
        // const newIndex = activeTab.index === 0 ? 0 : activeTab.index - 1
        // changeTab(tabs[newIndex]);
        changeTab(tabs[0]);
      }
    }
  }

  handleSubmit = (e) => {
    const { nextTab, name, type } = this.state;
    const length = this.props.tabs.length;
    if(type === 'add') {
      this.props.addTab({name:`${name !== '' ? name : 'tab'+nextTab}`, index: length, id: guid()}); // deleted index: nextTab
      this.setState({nextTab:nextTab + 1,visible: false, name: ''}); // deleted, nextTab: newTab
    } else if(typeof(type) === 'object') {
      // type is the tab object
      this.props.editTab(type, name);
      this.setState({visible: false, name: ''});
    }
  }


  closeModal = () => {
    this.setState({visible: false, name: ''})
  }

  openModal = () => {
    this.setState({visible: true}, () => {
      //console.log(this.nameInput);
      //this.nameInput.focus()
    })
  }

  handleChange = (e) => {
    e.preventDefault();
    this.setState({[e.target.name]: e.target.value});
  }

  render () {
    const { type } = this.state;
    const { tabs, ui } = this.props;
    const viewOnly = ui.viewOnly ? 'none' : ''
    const showHideClassName = `${this.state.visible ? 'modal-show' : 'modal-hide'}`;
    return ( < >
      <ul className='tabs' >
        {tabs && tabs.map(this.renderTab)}
        <FiPlus  name='add' style={{display:viewOnly, height:'1.75rem'}} size={'1.75rem'} className={`newtab-btn`} title='Create New Tab' onClick={(e) => this.handleAddTab(e)}/>
      </ul>
      <div className={`background ${showHideClassName}`} autoFocus={false}>
        <Draggable handle='div.tab-modal-header'>
          <div >
            <section className={`tab-modal`} >
                <div className='tab-modal-header' style={{cursor: 'move'}}>
                  <h1 style={{fontSize: '1.25rem'}}>{type === 'add' ? 'Add New Tab' : 'Edit Tab Name'}</h1>
                </div>
                <div className='modal-main' >
                  <div>
                    <InputText
                      placeholder='Tab Name'
                      ref={(input) => {this.nameInput = input;}}
                      name='name'
                      style={{width:'100%', marginBottom:'1rem'}}
                      value={this.state.name}
                      onChange={this.handleChange}
                    />
                  </div>
                  <div className='modal-footer'>
                    <Button label="Submit" icon="pi pi-check" onClick={(e) => this.handleSubmit(e)} />
                    <Button label="Close" icon="pi pi-times" onClick={this.closeModal} style={{marginLeft:'1rem', background: '#e53935', border: '1px solid #e53935'}} />
                  </div>
                </div>
            </section>
          </div>
        </Draggable>
      </div>
    </>)
  }
}

function mapStateToProps({ activeTab, tabs, ui }) {
  return { activeTab, tabs, ui };
}

export default connect(mapStateToProps, actions)(Tabs);
