import React, { Component } from 'react';
import { connect } from 'react-redux';
import Draggable from 'react-draggable';
import { InputText } from 'primereact/inputtext';
import { Checkbox } from 'primereact/checkbox';
import { Dropdown } from 'primereact/dropdown';
import { Button } from 'primereact/button';
import { Calendar } from 'primereact/calendar';
import { SelectButton } from 'primereact/selectbutton';
import MultiAxesConfig from './MultiAxesConfig';

const times = [
  {label: '5 minutes ago', value: 'five'},
  {label: '10 minutes ago', value: 'ten'},
  {label: 'half hour ago', value: 'halfhour'},
  {label: '1 hour ago', value: 'hour'}
];

const selectOptions = [
  {label: 'Live', value: 'live'},
  {label: 'Fixed', value: 'fixed'}
];

class MultiAxesLiveForm extends Component {
  state = { //form fields
    series: [],
    title: '',
    axes: [],
    liveStart: '',
    fixedStart:'',
    timeType: 'live',
    legend: true,
    visible: false
  }
  
  componentDidMount() {
    // populates fields when editing
    const { tile } = this.props.ui.modal;
    if(tile) {
      const { title, axes, series, liveStart, fixedStart, timeType, legend } = tile.properties;
      this.setState({
        title,
        axes,
        series,
        liveStart,
        fixedStart,
        timeType,
        legend
      });
    }
  }

  handleChange = (e) => {
    e.preventDefault();
    const {addData} = this.props;
    const value = e.target.checked ? e.target.checked : e.target.value;
    this.setState({[e.target.name]: value}, () =>{
      addData(this.state);
    });
  }

  openModal = (e) => {
    e.stopPropagation();
    e.preventDefault();
    this.setState({visible: true})
  }

  closeModal = (e) => {
    e.stopPropagation();
    e.preventDefault();
    this.setState({visible: false})
  }

  submitModal = (e, axes, series) => {
    e.preventDefault();
    const {addData} = this.props;
    this.setState({axes, series, visible: false}, () => {
      addData(this.state);
    });
  }

  renderTimeSelection = () => {
    const { timeType, liveStart, fixedStart, end } = this.state;
    if(timeType === 'live') {
      return (
        <Dropdown
          style={{width:'100%'}}
          name='liveStart'
          value={liveStart}
          onChange={this.handleChange}
          options={times}
          placeholder='Select a start time'
        />
      )
    } else if (timeType === 'fixed') {
      return (
        < >
          <span className='p-float-label'>
            <InputText
              className='input'
              type='number'
              min='0'
              max='168'
              value={fixedStart}
              name='fixedStart'
              onChange={this.handleChange}
            />
          <label htmlFor='fixedStart'>Hours Ago</label>
          </span>

        </>
      )
    } else {
      return <div>Something went wrong</div>
    }
  }

  render () {
    const { title, timeType, legend, series, axes } = this.state;
    const timeSelection = this.renderTimeSelection();
    const showHideClassName = `${this.state.visible ? 'modal-show' : 'modal-hide'}`;

    return (<>
      <section className='component-form' >
        <label>Time Type: </label>
        <SelectButton
          value={timeType}
          name='timeType'
          options={selectOptions}
          onChange={this.handleChange}
        />
        {timeSelection}
        <span className='p-float-label'>
          <InputText
            className='input'
            value={title}
            type='text'
            onChange={this.handleChange}
            name='title'
          />
          <label htmlFor='title'>Title</label>
        </span>
        <div>
          <Checkbox checked={legend} onChange={this.handleChange} name='legend' />
          <label htmlFor='legend' className='p-checkbox-label'>Display Legend</label>
        </div>
        <Button  style={{marginBottom:'1rem'}} onClick={this.openModal} label='Graph Config' />
      </section>
      <div className={`background ${showHideClassName}`} >
        <Draggable handle='div.tab-modal-header'>
          <div>
            <section className={`tab-modal `} style={{transform: 'translate(0,0)', left: 0, top: 0, width: '600px'}} >
              <div className='tab-modal-header' style={{cursor: 'move'}}>
                <h1>Axis and Plot Config</h1>
              </div>
              <div className='modal-main'>
                <MultiAxesConfig submitModal={this.submitModal} closeModal={this.closeModal} series={series} axes={axes} />
              </div>
            </section>
          </div>
        </Draggable>
      </div>
    </>)
  }
}

function mapStateToProps({data, ui}) {
  return {data, ui}
}

export default connect(mapStateToProps)(MultiAxesLiveForm);
