import React, { Component } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import { ScaleLoader } from 'react-spinners';
import { keys } from '../../util/keys';
import { getParameterByName } from '../../util';
import { FiRefreshCcw, FiSave } from 'react-icons/fi';
import { DataTable } from 'primereact/datatable';
import {Column} from 'primereact/column';
import { InputText } from 'primereact/inputtext';

class Table extends Component {
  state = {
    globalFilter: '',
    data: [],
    loading:true
  }

  componentDidUpdate(prevProps) {
    const { properties } = this.props.tile;
    if(prevProps.tile.properties !== properties) {
      this.setState({loading:true})
      this.fetchData();
    }
  }

  fetchData = async () => {
    const { option, search } = this.props.tile.properties;
    const id = getParameterByName('assetId');
    switch(option.value) {
      case 'dataItems':
        this.setState({loading: false, globalFilter: search})
        break
      case 'alarms':
        axios({
          url: `/api/Assets/${id}/Alarms`,
          baseURL: keys.BASE_URL,
          method: 'GET',
          headers: {
            "Content-Type": "application/json",
            "Authorization": keys.TOKEN,
          },
        })
        .then((res) => this.setState({data: res.data, loading:false, globalFilter: search}))
        break
      case 'events':
        axios({
          url: `/api/Assets/${id}/Events`,
          baseURL: keys.BASE_URL,
          method: 'GET',
          headers: {
            "Content-Type": "application/json",
            "Authorization": keys.TOKEN,
          },
        })
        .then((res) => this.setState({data: res.data, loading: false, globalFilter: search}))
        break
      case 'files':
        axios({
          url: `/api/assets/${id}`,
          baseURL: keys.BASE_URL,
          method: 'GET',
          headers: {
            "Content-Type": "application/json",
            "Authorization": keys.TOKEN,
          },
        })
        .then((res) => {
          const date = new Date(new Date() - (1000*60*60*24*7)).toJSON()
          const { serialNumber } = res.data;
          axios({
            url: `/api/files?hostname=${serialNumber}&startDate=${date}`,
            baseURL: keys.BASE_URL,
            method: 'GET',
            headers: {
              "Content-Type": "application/json",
              "Authorization": keys.TOKEN,
            },
          })
          .then((res) => this.setState({data: res.data, loading: false, globalFilter: search}))
        })
        break
      default:
        this.setState({loading: false});
        break
    }
  }

  export = () => {
    this.dt.exportCSV();
  }

  componentDidMount() {
    const { search } = this.props.tile.properties;
    this.setState({globalFilter: search})
    this.fetchData();
  }

  renderFileTable = () => {
    const { data } = this.state;
    const columns = [
      {header: 'File Name', field: 'filename'},
      {header: 'Host Name', field: 'hostname'},
      {header: 'haspId', field: 'haspId'},
      {header: 'Timestamp', field: 'timestamp'},
    ];
    const dynamicCols = columns.map((col, i) => {
      if(col.field === 'filename') {
        return (
          <Column
            key={i}
            field={col.field}
            header={col.header}
            sortable={true}
            body={(rowData, column) => (<a href={`/api/files/${rowData.fileId}/download`} >{`${rowData.filename}`}</a>)}
         />)
      } else {
        return <Column key={i} field={col.field} header={col.header} sortable={true} />
      }

    });
    return (
      <DataTable
        ref={(el) => { this.dt = el; }}
        value={data}
        rows={10}
        paginator={true}
        globalFilter={this.state.globalFilter}
        header={this.header}
      >
        {dynamicCols}
      </DataTable>
    )
  }

  renderEventTable = () => {
    const { data } = this.state;
    const columns = [
      {header: 'Name', field: 'name'},
      {header: 'Cause', field: 'cause'},
      {header: 'Recieved', field: 'received'},
      {header: 'Timestamp', field: 'timestamp'},
      {header: 'Count', field: 'count'}
    ]
    const dynamicCols = columns.map((col, i) => {
      return <Column key={i} field={col.field} header={col.header} sortable={true} />
    });
    return (
      <DataTable
        ref={(el) => { this.dt = el; }}
        value={data}
        rows={10}
        paginator={true}
        globalFilter={this.state.globalFilter}
        header={this.header}
      >
        {dynamicCols}
      </DataTable>
    )
  }

  renderAlarmTable = () => {
    const { data } = this.state;
    const columns = [
      {header:'Name', field: 'name'},
      {header:'Reason', field: 'reason'},
      {header: 'Cause', field: 'cause'},
      {header: 'Last Raised', field: 'raised'},
      {header: 'Severity', field: 'severity'},
      {header: 'Count', field: 'count'}
    ]
    const dynamicCols = columns.map((col, i) => {
      return <Column key={i} field={col.field} header={col.header} sortable={true} />
    });
    return (
      <DataTable
        ref={(el) => { this.dt = el; }}
        value={data}
        rows={10}
        paginator={true}
        globalFilter={this.state.globalFilter}
        header={this.header}
      >
        {dynamicCols}
      </DataTable>
    )
  }

  renderDataItemTable = () => {
    const { globalFilter } = this.state;
    const { currentData } = this.props;
    //const validRegEx = /^[^\\\/&]*$/;
    let data;
    if (currentData) {
      data = currentData.array.filter((di) => {
        try {
          return di.label.match(globalFilter)
        } catch(e) {
          return di.label.includes(globalFilter)
        }
      })
      .map(di => {
        return {
          ...di,
          value: Math.round(di.value * 1000) / 1000
        }
      });
    }
    const columns = [
      {header: 'Label', field: 'label'},
      {header: 'Value', field: 'value'},
    ];
    const dynamicCols = columns.map((col, i) => {
      return <Column key={i} field={col.field} header={col.header} sortable={true}/>
    });
    return (
      <DataTable
        ref={(el) => { this.dt = el; }}
        value={data}
        rows={10}
        paginator={true}
        header={this.header}
      >
        {dynamicCols}
      </DataTable>
    )
  }

  render () {
    const { loading, globalFilter } = this.state;
    const { option } = this.props.tile.properties;
    let renderTable;
    this.header = (<div style={{'textAlign':'left'}}>
      <InputText
        type="search"
        value={globalFilter}
        onChange={(e) => {
          e.stopPropagation();
          console.log(e.target.value);
          this.setState({globalFilter: e.target.value})
        }}
        placeholder="Search"
        size="50"
      />
      <span
        style={{marginLeft:'1rem', marginTop: '.5rem', cursor:'pointer', display:'inline-block'}}
        title='Refresh Data'
        onClick={(e) => {this.setState({loading:true});this.fetchData()}}
      >
        <FiRefreshCcw size='1rem' style={{height:'1rem'}} />
      </span>
      <span
        style={{marginLeft:'1rem', cursor:'pointer'}}
        title='Export data as csv'
        onClick={(e) => {this.export()}}
      >
        <FiSave size='1rem' style={{height:'1rem'}} onClick={this.export} />
      </span>
    </div>);
    if(loading) {
      return (
        <div
          style={{position:'absolute', top:'50%',left:'50%', transform: 'translate(-50%)'}}
        ><ScaleLoader sizeUnit={'px'} size={30} color={'#fff'} />
        </div>)
    } else {
      switch(option.value) {
        case 'dataItems':
          renderTable = this.renderDataItemTable();
          break
        case 'alarms':
          renderTable = this.renderAlarmTable();
          break
        case 'events':
          renderTable = this.renderEventTable();
          break
        case 'files':
          renderTable = this.renderFileTable();
          break
        default:
          renderTable = this.renderDataItemTable();
          break
      }
      return ( < >
        <div className='table-background' style={{height: '100%', width:'100%'}}>
          <div onMouseDown={e => e.stopPropagation()}>
            {renderTable}
          </div>
        </div>
      </> )
    }
  }
}

function mapStateToProps({ currentData }) {
  return ({ currentData });
}

export default connect(mapStateToProps)(Table);
