import React, { Component } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import { FiRefreshCcw, FiSave } from 'react-icons/fi';
import { ScaleLoader } from 'react-spinners';
import { keys } from '../../util/keys';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { InputText } from 'primereact/inputtext';
import variables from '../../styles/variables.scss';

class CustomTable extends Component {
  state = {
    globalFilter: '',
    data: [],
    loading: true
  }

  header = (<div style={{'textAlign':'left'}}>
    <i className="pi pi-search" style={{margin:'4px 4px 0 3rem'}}></i>
    <InputText
      type="search"
      onChange={(e) => {
        e.stopPropagation();
        this.setState({globalFilter: e.target.value})}
      }
      placeholder="Search"
      size="50"
    />
    <span
      style={{marginLeft:'1rem', cursor:'pointer'}}
      title='Refresh Data'
      onClick={(e) => {this.setState({loading:true});this.fetchData()}}
    >
      <FiRefreshCcw size='1rem' style={{height:'1rem'}} />
    </span>
    <span
      style={{marginLeft:'1rem', cursor:'pointer'}}
      title='Export data as csv'
      onClick={(e) => {this.export()}}
    >
      <FiSave size='1rem' style={{height:'1rem'}} onClick={this.export} />
    </span>
  </div>);

  fetchData = async () => {
    const { properties } = this.props.tile;
    const { asset } = this.props.data;
    const url = eval('`'+properties.url+'`');
    axios({
      url: url,
      baseURL: keys.BASE_URL,
      method: 'GET',
      headers: {
        "Content-Type": "application/json",
        "Authorization": keys.TOKEN,
      },
    })
    .then((res) => {
      this.setState({data: res.data, loading: false});
    })
    .catch((err) => {
      console.log(err);
      this.setState({loading:false});
    });
  }

  export = () => {
    this.dt.exportCSV();
  }

  componentDidMount() {
    this.fetchData();
  }

  componentDidUpdate(prevProps) {
    const { properties } = this.props.tile;
    if(prevProps.tile.properties !== properties) {
      this.fetchData();
    }
  }

  linkTemplate = (rowData, column, body) => {
    const cell = body(rowData).replace(/\^/g,' ');
    return (<div dangerouslySetInnerHTML={{__html: cell}}></div>)
  }

  renderTable = () => {
    const {data, loading} = this.state;
    if(loading) {
      return (
        <div
          style={{position:'absolute', top:'50%',left:'50%', transform: 'translate(-50%)'}}
        >
          <ScaleLoader sizeUnit={'px'} size={30} color={variables.fontColor} />
        </div>)
    } else {
      try {
        const columns  = eval(this.props.tile.properties.columns.replace(/\s/g,''));
        const dynamicCols = columns.map((col, i) => {
          if (col.body) {
            return <Column key={i} field={col.field} header={col.header} sortable={true} body={(rowData, column) => this.linkTemplate(rowData, column, col.body)}/>
          } else {
            return <Column key={i} field={col.field} header={col.header} sortable={true} />
          }
        });
        return (
          <DataTable
            ref={(el) => { this.dt = el; }}
            value={data}
            rows={10}
            paginator={true}
            globalFilter={this.state.globalFilter}
            header={this.header}
          >
            {dynamicCols}
          </DataTable>
        )
      } catch(err) {
        return (
          <div
            style={{position:'absolute', top:'50%',left:'50%', transform: 'translate(-50%)'}}
          >Invalid Input, please try again
          </div>)
      }
    }
  }

  render () {
    const table = this.renderTable();
    return ( < >
      <div className='table-background' style={{height: '100%', width:'100%'}}>
        <div onMouseDown={e => e.stopPropagation()}>
          {table}
        </div>
      </div>
    </> )
  }
}

function mapStateToProps({ data }) {
  return ({ data });
}

export default connect(mapStateToProps)(CustomTable);
