const ui = (state={sidebar: false, modal: {visible:false, tile:null}, viewOnly: true}, action) => {
  switch(action.type) {
    case 'TOGGLE_SIDEBAR':
    const sidebar = !state.sidebar;
      return {
        ...state,
        sidebar
      }
    case 'TOGGLE_MODAL':
      return {
        ...state,
        modal: {
          visible: true,
          tile: action.tile
        }
      }
    case 'RESET_MODAL': {
      return {
        ...state,
        modal: {
          visible: false,
          tile: null
        }
      }
    }
    case 'EDIT_TILE': {
      // action has tile
      return {
        ...state,
        modal: {
          ...state.modal,
          tile: action.tile,
        }
      }
    }
    case 'TOGGLE_VIEWONLY': {
      return {
        ...state,
        viewOnly: !state.viewOnly
      }
    }
    default:
      return state;
  }
}

export default ui;
