const tabs = (state=[{name: 'default', index:0}], action) => {
  switch(action.type) {
    case 'ADD_TAB':
      return [
        ...state,
        action.tab
      ];
    case 'EDIT_TAB': {
      return state.map((tab) => {
        if(tab.id === action.tab.id) {
          return {
            ...tab,
            name: action.name
          }
        } else {
          return tab;
        }
      })
    }
    case 'DELETE_TAB':
      return state.filter((tab, i) => i !== action.index)
        .map((tab, i) => {
          if (tab.index > action.index) {
            return {
              name: tab.name,
              index: tab.index - 1
            };
          } else {
            return tab;
          }
        });
    case 'LOAD_DASHBOARD':
    if(action.payload.data.tabs) {
      return action.payload.data.tabs;
    } else {
      return state;
    }
    default:
      return state
  }
}

export default tabs;
