const tiles = (state=[[]], action) => {
  switch(action.type) {
    case 'ADD_TILE': {
      const { activeTab, id, tile } = action;
      const addTiles = state;
      const addedTile = [...state[activeTab.index], {id: id, properties: tile.properties, dataGrid: tile.dataGrid}];
      addTiles.splice(activeTab.index, 1, addedTile);
      return [...addTiles];
    }
    case 'DELETE_TILE': {
      const { activeTab, id } = action;
      const newTiles = state[activeTab.index].filter(tile => tile.id !== id);
      const tiles = state.slice(0);
      tiles.splice(activeTab.index, 1, newTiles);
      return [...tiles];
    }
    case 'EDIT_TILE': {
      const { index, newTile } = action;
      const newTiles = state[index].filter((tile) => tile.id !== newTile.id);
      newTiles.push(newTile);
      const tiles = state.slice(0);
      tiles.splice(index, 1, newTiles);
      return [...tiles];
    }
    case 'ADD_TAB':
      return [
        ...state,
        []
      ]
    case 'DELETE_TAB':
      // filter state with index of tab
      return state.filter((tiles, i) => i !== action.index);
    case 'LOAD_DASHBOARD':
      if(action.payload.data.tiles) {
        return action.payload.data.tiles
      } else {
        return state;
      }
    default:
      return state;
  }
}

export default tiles;
