import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Dropdown } from 'primereact/dropdown';
import { InputText } from 'primereact/inputtext';

const tableOptions = [
  {label:'DataItems', value:'dataItems'},
  {label:'Files', value:'files'},
  {label:'Alarms', value:'alarms'},
  {label:'Events', value:'events'},
]

class TableForm extends Component {
  state = {
    option: '',
    search: ''
  }

  componentDidMount() {
    const { tile } = this.props.ui.modal;
    if(tile) {
      const { option, search } = tile.properties;
      this.setState({
        option,
        search
      })
    }
  }

  handleChange = (e) => {
    e.preventDefault();
    const {addData} = this.props;
    this.setState({[e.target.name]: e.target.value}, () =>{
      addData(this.state);
    });
  }

  render () {
    const { option, search } = this.state;
    return (
      <section className='component-form'>
        <Dropdown
          style={{width:'100%'}}
          name='option'
          value={option}
          options={tableOptions}
          optionLabel='label'
          onChange={this.handleChange}
          placeholder='Choose Table Type'
          required
        />
        <span className='p-float-label'>
          <InputText
            value={search}
            type='text'
            onChange={this.handleChange}
            name='search'
          />
        <label htmlFor='search'>Search</label>
        </span>
      </section>
    )
  }
}

function mapStateToProps({ ui }) {
  return { ui };
}

export default connect(mapStateToProps)(TableForm);
