const layouts = (state=[{}], action) => {
  switch(action.type) {
    case 'SAVE_LAYOUT':
      const { layouts, tab } = action;
      const newLayouts = state.slice(0);
      newLayouts.splice(tab.index, 1, layouts);
      return newLayouts;
    case 'ADD_TAB':
      return [
        ...state,
        {}
      ];
    case 'DELETE_TAB':
      // filter state with index of tab
      return state.filter((layouts, i) => i !== action.index);
    case 'SAVE_DASHBOARD' :
      return state;
    case 'LOAD_DASHBOARD':
      if(action.payload.data.layouts) {
        return action.payload.data.layouts;
      } else {
        return state;
      }
    default:
      return state;
  }
}

export default layouts;
