import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";

class CylinderGauge extends Component {
	static defaultProps = {}

	chartInit = () => {
		const { id, properties } = this.props.tile;
		const max = Number(properties.max);
		const dataItem = properties.dataItem;
		const color = properties.color;

		am4core.options.commercialLicense = true;
		this.chart = am4core.create(id, am4charts.XYChart3D);
		this.chart.paddingRight = 5;
		this.chart.paddingLeft = 0;
		this.chart.paddingTop = 5;
		this.chart.paddingBottom = 0;
		this.chart.marginRight = 5;
		this.chart.marginLeft = 0;
		this.chart.marginTop = 5;
		this.chart.marginBottom = 0;

		const title = this.chart.createChild(am4core.Label);
		title.text = `Max: ${max}`;
		title.align = 'center';
		const label = this.chart.createChild(am4core.Label);
		label.text = properties.name !== '' ? properties.name : `${dataItem.label}`;
		label.align = "center";
		label.isMeasured = false;
		label.x = am4core.percent(50);
		label.horizontalCenter = 'middle';
		label.y = am4core.percent(0);

		const categoryAxis = this.chart.xAxes.push(new am4charts.CategoryAxis());
		categoryAxis.dataFields.category = "category";
		categoryAxis.renderer.grid.template.location = 0;
		categoryAxis.renderer.grid.template.strokeOpacity = 0;

		const valueAxis = this.chart.yAxes.push(new am4charts.ValueAxis());
		valueAxis.renderer.grid.template.strokeOpacity = 0;
		valueAxis.min = -20;
		valueAxis.max = 120;
		valueAxis.strictMinMax = true;
		valueAxis.renderer.baseGrid.disabled = true;
		valueAxis.numberFormatter = new am4core.NumberFormatter();
		valueAxis.numberFormatter.numberFormat = "";
		// valueAxis.numberFormatter.bigNumberPrefixes = [
		// 	{ "number": 1000, "suffix" : "k"},
		// 	{ "number": 1000000, "suffix": "M" },
		// 	{ "number": 1e+9, "suffix": "G" },
		// ]
		// valueAxis.numberFormatter.numberFormat = '#,###.#a';
		// valueAxis.renderer.labels.template.adapter.add("text", function(text) {
		//   if ((text > 100) || (text < 0)) {
		//     return "";
		//   }
		//   else {
		//     return text + "%";
		//   }
		// });

		const series1 = this.chart.series.push(new am4charts.ConeSeries());
		series1.dataFields.valueY = "fillValue";
		series1.dataFields.categoryX = "category";
		series1.columns.template.width = am4core.percent(80);
		series1.columns.template.fillOpacity = 0.9;
		series1.columns.template.strokeOpacity = 1;
		series1.columns.template.strokeWidth = 2;
		if(color !== '') {
			series1.columns.template.fill = `#${color}`;
			series1.columns.template.stroke = `#${color}`;
		}

		const series2 = this.chart.series.push(new am4charts.ConeSeries());
		series2.dataFields.valueY = "emptyValue";
		series2.dataFields.categoryX = "category";
		series2.stacked = true;
		series2.columns.template.width = am4core.percent(80);
		series2.columns.template.fill = am4core.color("#000");
		series2.columns.template.fillOpacity = 0.1;
		series2.columns.template.stroke = am4core.color("#000");
		series2.columns.template.strokeOpacity = 0.2;
		series2.columns.template.strokeWidth = 2;

		this.updateValue();
	}

	componentDidMount() {
		this.chartInit()
	}

	updateValue = () => {
		const { properties } = this.props.tile;
		const { currentData } = this.props;
		try {
			const newData = currentData.object[properties.dataItem.dataItemId].value;
			this.chart.data = [
				{
					'category': `${Math.round(newData*100)/100} ${properties.unit}`,
					'fillValue': (newData/properties.max)*100, // (value/max)*100
					'emptyValue': ((properties.max - newData)/properties.max)*100 // ((max-value)/max)*100
				}
			];
		} catch(e) {
			this.chart.data = [
				{
					'category': `undefined`,
					'fillValue': (0)*100, // (value/max)*100
					'emptyValue': (0)*100 // ((max-value)/max)*100
				}
			];
		}
	}

	componentDidUpdate(prevProps) {
		const { properties } = this.props.tile;

		if(prevProps.tile.properties !== properties) {
			this.chart.dispose();
			this.chartInit();
		}
		// update on new current data call
		this.updateValue();
	}

	componentWillUnmount() {
	 if (this.chart) {
		 this.chart.dispose();
	 }
  }

	render() {
		const { id } = this.props.tile;
		return (
			<div
				id={id}
				style={{width: "100%", height: "100%"}}
			>
			</div>
		)
	}
}

function mapStateToProps({ currentData }) {
	return { currentData }
}

export default connect(mapStateToProps)(CylinderGauge)
