import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Editor } from 'primereact/editor';
import { PanelMenu } from 'primereact/panelmenu';

class TextBoxForm extends Component {
  state = {
    text: this.props.ui.modal.tile ? this.props.ui.modal.tile.properties.text : '',
    items:[]
  }
  componentDidMount() {
    const { currentData } = this.props;
    const { attributes } = this.props.data;
    this.setState({items:[
      {
        label:'Data Items',
        items: currentData.array.map((di)=>{
          return {
            label:di.label,
            items:[{
              label:"Label",
              command:() => {
                this.addText(`\${currentData.object['${di.dataItemId}'].label}`)
              }
            },
            {
              label:"Value",
              command:() => {
                this.addText(`\${currentData.object['${di.dataItemId}'].value}`)
              }
            },
            {
              label:"Timestamp",
              command:() => {
                this.addText(`\${currentData.object['${di.dataItemId}'].timestamp}`)
              }
            },
            {
              label:"Unit",
              command: () => {
                this.addText(`\${currentData.object['${di.dataItemId}'].unit}`)
              }
            }
          ]
        }
      })
      },
      {
        label:'Attributes',
        items: attributes.array.map((att) => {
          return {
            label:att.attributeName,
            items:[{
              label:"Name",
              command:() => {
                this.addText(`\${attributes.object['${att.attributeName}'].attributeName}`)
              }
            },
            {
              label:"Value",
              command:() => {
                this.addText(`\${attributes.object['${att.attributeName}'].attributeValue}`)
              }
            }
          ]
        }
      })
      }
    ]
    })
  }

  handleChange = (e) => {
    const { addData } = this.props;
    this.setState({text: e.htmlValue}, () => {
      addData({text: this.state.text});
    });
  }

  addText = (string) => {
    const { text } = this.state;
    this.setState({text: text + string});
  }

  render () {
    const { text } = this.state;
    return (
      <section className='component-form' >
        <Editor
          style={{display:'inline-block', height: '300px', width: 'inherit'}}
          name='text'
          value={text}
          onTextChange={this.handleChange}
          placeholder='Your text here'
        />
        <div style={{overflow:'scroll', maxHeight: '200px'}}>
          <PanelMenu model={this.state.items} style={{overflow:'scroll'}}/>
        </div>
      </section>
    )
  }
}

function mapStateToProps({ ui, data, currentData }) {
  return { ui, data, currentData }
}
export default connect(mapStateToProps)(TextBoxForm);


