import React, { Component } from 'react';
import { connect } from 'react-redux';
import { InputText } from 'primereact/inputtext';
import { MultiSelect } from 'primereact/multiselect';

class SolidGaugeForm extends Component {
  state = {
    dataItems: [],
    max: '',
    unit: ''
  }

  dataItems = this.props.data.dataItemDefinitions.array;

  componentDidMount() {
    // populates fields when editing
    const { tile } = this.props.ui.modal;
    if(tile) {
      const { dataItems, max } = tile.properties;
      this.setState({
        dataItems,
        max
      });
    }
  }

  handleChange = (e) => {
    e.preventDefault();
    const {addData} = this.props;
    this.setState({[e.target.name]: e.target.value}, () =>{
      addData(this.state);
    });
  }

  render() {
    const { dataItems, max, unit } = this.state;
    console.log(this.dataItems)
    return (
      <div className='component-form'>
        <MultiSelect
          name='dataItems'
          value={dataItems}
          options={this.dataItems}
          optionLabel='label'
          onChange={this.handleChange}
          placeholder='Choose data items'
          filter
          required
        />
        <span className='p-float-label'>
        <InputText
          value={max}
          type='number'
          onChange={this.handleChange}
          name='max'
        />
      <label htmlFor='max'>Max*</label>
        </span>
        <span className='p-float-label'>
        <InputText
          value={unit}
          type='text'
          onChange={this.handleChange}
          name='unit'
        />
        <label htmlFor='unit'>Unit</label>
        </span>
      </div>
    );
  }
}

function mapStateToProps({data, ui}) {
  return {data, ui}
}

export default connect(mapStateToProps)(SolidGaugeForm);
