import React from 'react';
import { connect } from 'react-redux';
import { getParameterByName } from '../util';
import * as actions from '../actions';
import { FiMenu } from 'react-icons/fi';
import { Checkbox } from 'primereact/checkbox';

function Navbar(props) {
	const { toggleSidebar, toggleViewOnly, ui } = props;
	const { asset, assetDef } = props.data;
	const viewOnly = ui.viewOnly ? 'hidden' : ''
	const show = window.innerWidth > 600 ? 'block' : 'none';
	const name = asset && assetDef ?
	 getParameterByName('mode') === 'builder' ? assetDef.assetDefinitionName : asset.assetAlias
	 : '';
	return (
		<nav className="navbar">
			<div className='navbar-menu navbar-section' onClick={toggleSidebar} style={{cursor:'pointer', visibility: viewOnly}} >
				<FiMenu style={{height:'1.75rem'}} className='navbar-menu-icon' size={'1.75rem'} />
			</div>
			<div className='navbar-section'>
				<Checkbox
					style={{marginTop:'.2rem', marginLeft:'1.5rem', borderRadius: '4px', backgroundColor:'#444 '}}
					title='Click to toggle'
					className=''
					name='viewOnly'
					checked={ui.viewOnly}
					onChange={toggleViewOnly}
				/>
				<label
					style={{color:'white', fontSize: '.75rem', marginLeft:'.5rem', }}
					title='Click checkbox to toggle'
					htmlFor="viewOnly"
					className="p-checkbox-label navbar-checkbox-label"
				>View only mode
				</label>
			</div>
			<h1 style={{margin: 0, marginLeft: '1rem'}} className="navbar-asset">{`${name}`}</h1>
			<h1 style={{display: show}} className="navbar-brand navbar-section">Lime Cloud</h1>
			<div  className='navbar-section' style={{marginLeft:'auto', display: show}}>
				<ul style={{listStyle: 'none', margin: 0}}>
					<li className='navbar-navitem'><a href='../assets/list.shtml'>Assets</a></li>
					<li className='navbar-navitem'><a href='../assetdefinitions/index.shtml'>Asset Definitions</a></li>
				</ul>
			</div>
		</nav>
	)
}

function mapStateToProps({ ui, data }) {
	return { ui, data }
}

export default connect(mapStateToProps,actions)(Navbar);
